@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap);

body{
	margin:0px;
	padding:0px;
	font-family: 'Poppins', sans-serif;
	font-size:16px;
	line-height:28px;
}

img{
	max-width:100%;
}

.App, header, .container-fluid {
    width: 100%;
    margin: 0px;
    padding: 0px;
}



/*Header*/

header .bg-light {
    background: #EE4266 !important;
    width: 100%;
}

header {
    background: #daac00;
}

header .navbar-nav li a {
    font-size: 20px;
    color: #FFF !important;
}

.navbar-brand {
    margin-left: 2%;
}



/*Banner*/

section.banner-section {
    background: #fcc302;
    padding: 100px 0px;
}

#contac_us_btn {
    background-color: #5E1675;
}

.banner-text h1 {
    color: #FFF;
    font-size: 56px;
    line-height: 68px;
    margin-bottom: 20px;
}

.banner-text p {
    color: #FFF;
    font-size: 22px;
    margin-bottom: 33px;
}

.banner-text {
    max-width: 541px;
}

.btn {
    font-size: 16px;
    padding: 10px 22px;
}

.btn-blue {
    background: #00bec9;
    color: #FFF;
    border: 2px solid #00bec9;
    margin-right: 20px;
}

.btn-outline-light {
    border: 2px solid #FFF;
}


/*text section*/

section.text-section {
    width: 100%;
    padding: 100px 0px;
}

h2 {
    font-size: 40px;
    font-weight: 600;
}

p {
    font-size: 20px;
}

.btn-primary {
    background: #fcc302;
    border-color: #fcc302;
}

.btn-primary:hover {
    background: #fcc302 !important;
    border-color: #fcc302 !important;
    opacity: 0.9;
}

.text-col {
    max-width: 466px;
}

.text-col h2, .text-col p {
    margin-bottom: 20px;
}

.right-img {
    position: relative;
    padding-left: 30px;
}

.right-img:before {
    content: "";
    width: 250px;
    height: 250px;
    background: #fcc302;
    position: absolute;
    z-index: -1;
    left: -1px;
    bottom: -32px;
}


/*Services*/

section.services-section {
    background: #fcc302;
    padding: 100px 0px 70px;
    text-align: center;
    color: #FFF;
    width: 100%;
}

section.services-section .section-title {
    margin-bottom: 10px;
    color: #FFF;
}

.services-box h3 {
    color: #FFF;
}

.services-box {
    margin-bottom: 30px;
}

.services-box p {
    color: #FFF;
}

.services-img {
    max-width: 340px;
    margin: 0px auto 30px;
}

.services-img img {
    border: 5px solid #FFF;
}

section.text-section.animal-section .text-col {
    text-align: right;
}



/*Shop*/

section.shop-section {
    padding: 150px 0px 70px;
    background: #fcc302;
    text-align: center;
    color: #FFF;
}

section.shop-section {
    padding-bottom: 10%;
}

.shop-box h5 {
    font-weight: 500;
    color: #5E1675;
}

.intro-main {
    max-width: 500px;
    margin: 0px auto 50px;
}

.shop-box .btn {
    border-radius: 58px;
    padding: 10px 38px;
    background-color: #EE4266;
}

.shop-img {
    max-width: 345px;
    margin: 0px auto 30px;
    border: 5px solid #FFF;
}

.shop-img img {
    width: 100%;
}

.price {
    font-size: 28px;
    margin-bottom: 16px;
    color: #000;
}

.price span {
    margin: 0px 10px;
    font-weight: 600;
}

span.offer-price {
    text-decoration: line-through;
}

.shop-box {
    margin-bottom: 30px;
}



/*Team */

section.team-section {
    padding: 100px 0px 70px;
    text-align: center;
}

.team-img {
    max-width: 300px;
    margin: 0px auto 30px;
}

.team-box {
    margin-bottom: 30px;
}

.team-info h3 {
    font-size: 24px;
    font-weight: 700;
}

ul.team-social {
    margin: 0px;
    padding: 0px;
}

ul.team-social li {
    display: inline-block;
}

ul.team-social li a {
    width: 30px;
    height: 30px;
    background: #fcc302;
    display: inline-block;
    line-height: 30px;
    color: #FFF;
    border-radius: 50%;
}


/*Stats */


.stats-section{
	background:url(/pet_hospital/static/media/bg.4e87d758.jpg) no-repeat;
}

.stats-section {
    background-size: cover !important;
    padding: 50px 0px 70px;
    height: 100vh;
}

.stats-box {
   
    text-align: center;
    
}

.stats-box h2 {
    color: #FFF;
    font-size: 30px;
    margin-top: 20px;
}

.stats-box h3 {
    color: #FFF;
    font-weight: 700;
}



/*Blog*/

section.blog-section {
    padding: 100px 0px 70px;
}



.blog-info h3 {
    font-size: 22px;
}

.blog-img img {
    width: 100%;
}

.blog-img {
    margin: -20px -20px 23px;
}

 

.blog-box {
    margin: 0px auto 30px;
    border: 2px solid #ccc;
    padding: 20px;
    max-width: 400px;
}

a.link-more {
    text-decoration: none;
    color: #00bec9;
    font-weight: 500;
}

span.post-date {
    color: #00bec9;
    display: inline-block;
    margin: 11px 0px;
}




/*Footer*/

.footer {
  position: relative;
  width: 100%;
  background: #3586ff;
  min-height: 100px;
  padding: 20px 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.social-icon,
.menu {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px 0;
  flex-wrap: wrap;
}

.social-icon__item,
.menu__item {
  list-style: none;
}

.social-icon__link {
  font-size: 2rem;
  color: #fff;
  margin: 0 10px;
  display: inline-block;
  transition: 0.5s;
}
.social-icon__link:hover {
  transform: translateY(-10px);
}

.menu__link {
  font-size: 1.2rem;
  color: #fff;
  margin: 0 10px;
  display: inline-block;
  transition: 0.5s;
  text-decoration: none;
  opacity: 0.75;
  font-weight: 300;
}

.menu__link:hover {
  opacity: 1;
}

.footer p {
  color: #fff;
  margin: 15px 0 10px 0;
  font-size: 1rem;
  font-weight: 300;
}

.wave {
  position: absolute;
  top: -100px;
  left: 0;
  width: 100%;
  height: 100px;
  background: url("https://i.ibb.co/wQZVxxk/wave.png");
  background-size: 1000px 100px;
}

.wave#wave1 {
  z-index: 1000;
  opacity: 1;
  bottom: 0;
  animation: animateWaves 4s linear infinite;
}

.wave#wave2 {
  z-index: 999;
  opacity: 0.5;
  bottom: 10px;
  animation: animate 4s linear infinite !important;
}

.wave#wave3 {
  z-index: 1000;
  opacity: 0.2;
  bottom: 15px;
  animation: animateWaves 3s linear infinite;
}

.wave#wave4 {
  z-index: 999;
  opacity: 0.7;
  bottom: 20px;
  animation: animate 3s linear infinite;
}

@keyframes animateWaves {
  0% {
    background-position-x: 1000px;
  }
  100% {
    background-positon-x: 0px;
  }
}

@keyframes animate {
  0% {
    background-position-x: -1000px;
  }
  100% {
    background-positon-x: 0px;
  }
}



